@import './reset.css';

:root {
    --app-text-color : rgb(255, 255, 255, 0.9);
    --app-blue: #26329b;
    --app-purple: #a4508b;  
    --app-orange: #c47f3e ;
}

main {
    display: grid;
    place-items: center;
    height: 100vh;
}

.container {
    width: min(100% - 30px, 1200px);
    margin-inline: auto;
}


/* -------------------------------- */
/* Search Page                      */
/* -------------------------------- */

.search-page {
    text-align: center;
}

.fa-github-alt {
    font-size: 7rem;
    color: var(--app-orange);
}

form {
    border: none;
    width: 650px;
    height: 80px;
    padding: 15px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

input[type = "text"] {
    width: 75%;
    height: 50px;
    padding: 0 15px;
    border: 1px solid rgb(110 110 110/ 0.15);
    background-color: rgb(148 146 146/ 0.1);
    border-radius: 5px;
    box-shadow: rgb(0 0 0/ 0.1) 0px 20px 20px -8px, rgb(0 0 0 /0.1) 0px 10px 10px -10px;
    font-size: 16px;
    font-weight: 400;
    color: #fff;
    text-align: left;
    transition: all 0.30s ease-in-out;
}

input[type = "text"]:focus {
    border: 1px solid rgb(110 110 110/ 0.3);
}

h4 {
    font-size: 30px;
    color: rgb(51, 51, 51, 0.8);
    font-weight: 500;
    text-align: center;
}

input[type="submit"] {
    width: 130px;
    height: 50px;
    color: #fff;
    font-size:14px;
    border-radius: 5px;
    background-color: var(--app-orange);
    text-transform: uppercase;
    transition: all 0.25s ease-in-out;
}

input[type="submit"]:hover {
    background-color: #ad6f35;
}

.error-message {
    margin-top: 2px;
    font-size: 16px;
    width: 180px;
    height: 24px;
    color: #fff;
    padding: 0px 15px;
    visibility: hidden;
    opacity: 0;
    text-align: left;
}


.is-visible {
    visibility: visible;
    opacity: 1;
}

/* -------------------------------- */
/* User Page                        */
/* -------------------------------- */

section {
    margin-top: 6rem;
    width: 100%;
    height: 100%;
    padding: 20px;
}

.profile {
    padding: 15px 0px;
    border-radius: 10px;
    width: 45%;
    margin-inline: auto;
}

.profile-image {
    border: none;
    width: 250px;
    height: 250px;
    border-radius: 50%;
    margin-inline: auto;
}

.profile a {
    margin-left: 15px;
}

.fa-up-right-from-square {
    color: rgb(255, 255, 255, 0.6);
    transition: all 0.35s ease-in-out;
    font-size: 30px;
}

.fa-up-right-from-square:hover {
    color: var(--app-orange);
}

.fa-github {
    color: var(--app-orange);
}

h4 {
    width: 550px;
    text-align: center;
    color: var(--app-text-color);
}


img {
    max-width: 100%;
    max-height: 100%;
    border-radius: 50%;
}

h2 {
    font-size: 2.8rem;
    font-weight: 500;
    color: var(--app-text-color);
    letter-spacing: 0.30px;
    line-height: 50px;
    text-align: center;
    margin-top: 20px;
}

.info {
    margin-inline: auto;
    width: 480px;
    margin-top: 1.5rem;
    border: none;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.count {
    width: 150px;
    height: auto;
    border: none;
    padding: 5px;
    text-align: center;
}

.count span {
    font-size: 2rem;
    color: var(--app-text-color);
}

.count p {
    color: var(--app-orange);
}

p {
    color: var(--app-text-color);
}


/* -------------------------------- */
/* Repo List Display                */
/* -------------------------------- */

section.repo-display{
    margin-top: 3rem;
    width: 100%;
    height: 100%;
}

.repolist{
    margin-top: 4px;
    border: none;
    height: auto;
    width: 50%;
    padding: 10px 10px 30px 10px;
    margin-inline: auto;
    display: grid;
    place-items: center;
    gap: 15px;
    grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
    grid-template-rows: repeat(auto-fill, minmax(80px, 1fr));
}

.repo h3 {
    letter-spacing: 0.30px;
    font-weight: 400;
    font-size: 1.5rem;
}

.repo h3 a {
    text-decoration: none;
    color: var(--app-orange);
    transition: all 0.35s ease-in-out;
}

.repo h3 a:hover {
    text-decoration: underline;
}

.repo p {
    margin-top: 3px;
    color: rgb(255, 255, 255, 0.6);
}


.repo {
    width: 550px;
    height: auto;
    padding: 20px 15px;
    background-color: #282a3b;
    border-radius: 10px;
    transition: all 0.35s ease-in-out;
    border: 1px solid transparent;
}

.repo:hover{
    border: 1px solid rgb(255, 255, 255,0.05);
}



